@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  background-color: #f5f3f3;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.mobile,
.tablet,
.desktop{
  width: 100%;
  display: -webkit-flex;
  display: flex;
  height: 100vh;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 3rem;
}

.observations{
  height: 100vh;
  overflow: hidden;
}

.desktop-container {
  display: -webkit-flex;
  display: flex; /* or inline-flex */
  -webkit-flex-direction: row;
          flex-direction: row;
}

.desktop-body{
  
  width: 100%;
  background-color: #f5f3f3;
  height: 100vh;
  overflow-y: scroll;
}
#profile-card{
    box-shadow: 0 0 13px 1px rgba(0, 0, 0, 0.12);
    margin: 20px;
    margin-bottom: 10px;
    border-radius: 12px;
}

#profile-card .profile-image{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding-top: 15px;
    margin-bottom: 10px;
}

#profile-card .profile-username{
    width: 100%;
    text-align: center;
    color: rgb(36, 36, 36);
    font-weight: 900;
}

#profile-card .profile-level{
    width: 100%;
    text-align: center;
    color: rgb(141, 141, 141);
    font-weight: 200;
    padding-bottom: 15px;
}
    
#navbar .pro-sidebar {
  
  height: 100vh;
  width: 275px;
}

#navbar{
  width: 275px;
}

#navbar .closemenu {
  color: #000;
  position: absolute;
  right: 0;
  z-index: 9999;
  line-height: 20px;
  border-radius: 50%;
  font-weight: bold;
  font-size: 22px;
  top: 55px;
  cursor: pointer;
}
#navbar .pro-sidebar {
  width: 100%;
  min-width: 100%;
}
#navbar .pro-sidebar.collapsed {
  width: 80px;
  min-width: 80px;
}
#navbar .pro-sidebar-inner {
  
  background-color: white;
  box-shadow: 0.5px 0.866px 2px 0px rgba(0, 0, 0, 0.15);
}
#navbar .pro-sidebar-inner .pro-sidebar-layout {
  overflow-y: hidden;
  
  border-width: 3px;
  
}
#navbar .pro-sidebar-inner .pro-sidebar-layout .logotext p {
  font-size: 20px;
  padding: 20px;
  color: #000;
  font-weight: bold;
}
#navbar .pro-sidebar-inner .pro-sidebar-layout ul {
  
  padding: 0 0px;
}
#navbar .pro-sidebar-inner .pro-sidebar-layout ul .pro-inner-item {
  color: rgb(141, 141, 141);
  margin: 25px 0px; /* Change spacing between menu elements here */ 
  font-weight: bold;
}
#navbar .pro-sidebar-inner .pro-sidebar-layout ul .pro-inner-item .pro-icon-wrapper {
  width: 50px;
  height: 25px;
  background-color: white;
  color: #000;
  border-radius: 3px;
}

.home-menuText,
.observation-menuText,
.battle-menuText{
  height: 20px; /* Change icon size here */
  width:30px;
  color: rgb(141, 141, 141);
}

.active .home-menuText{
  color:rgb(131, 56, 236, 1);
  transition: color 100ms;
}

.active .observation-menuText{
  color:rgb(58, 134, 255, 1);
  transition: color 100ms;
}

.active .battle-menuText{
  color:rgb(255, 0, 110, 1);
  transition: color 100ms;
}

.home-menuItem,
.observation-menuItem,
.battle-menuItem{
  border-color:white;
}

.logout-text{
  color:rgb(36, 36, 36);
}

.active .menu-text{
  color:rgb(36, 36, 36);
  transition: color 100ms;
}

#navbar .pro-sidebar-inner .pro-sidebar-layout ul .home-menuItem .active {
  border-right-style: solid;
  border-width: 2.5px;
  border-color:rgb(131, 56, 236, .8);
  transition: border-color 300ms;
}

#navbar .pro-sidebar-inner .pro-sidebar-layout ul .observation-menuItem .active{
  border-right-style: solid;
  border-width: 2.5px;
  border-color:rgb(58, 134, 255, .8);
  transition: border-color 300ms;
}

#navbar .pro-sidebar-inner .pro-sidebar-layout ul .battle-menuItem .active{
  border-right-style: solid;
  border-width: 2.5px;
  border-color:rgb(255, 0, 110, .8);
  transition: border-color 300ms;
}


