#navbar .logo {
  padding: 20px;
}

@media only screen and (max-width: 720px) {
  html {
    overflow: hidden;
  }
}
#dashboard{
    padding: 50px;
}

#dashboard .dashboard-header{
    box-shadow: 2px 2px 20px 5px rgba(0, 0, 0, 0.08);
    display: -webkit-inline-flex;
    display: inline-flex;
    overflow: hidden;
    background-color: white;
    margin-left:15px;
    margin-bottom: 35px;
    width: calc(100% - 30px);
    border-radius: 20px;
}

#dashboard .dashboard-header-text{
    width: 50%;
    padding-left: 7%; 
    padding-right: 7%;
    padding-top: 5%; 
    padding-bottom: 5%;
}

#dashboard .dashboard-header-text-header{
    color: rgb(36, 36, 36);
    font-size: 35px;
    position: relative;
    width: calc(100%);
    height: calc(50% - 20px);
}

#dashboard .dashboard-header-text-text{
    color: rgb(36, 36, 36);
    overflow: hidden;
    position: relative;
    width: calc(100%);
    top: 20px;
    height: calc(50%);
}

#dashboard .dashboard-header-image {
    width: 50%;
}

#dashboard .dashboard-card {
    margin: 15px; 
    margin-top: 30px;
    
}

.Observation {
    cursor: pointer;
    transition: all 0.3s ease-out;

}

.Observation:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.btn {
    border-style: none;
    background-color: transparent;
}

.btn-primary {
    color: transparent!important;
    background-color: transparent!important;
    border-color: transparent!important;
    background-image: none;
    box-shadow: inset 0 3px 5px rgba(0,0,0,0)!important;
}

.card {
    border: none;
    border-color: transparent;
    background-color: transparent;
}

.dropDown-button {
    background-color: white!important;
    color: black!important;
}
.popup {
    z-index: 10;
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.7);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: baseline;
            align-items: baseline;
    overflow-x: visible;
    overflow-y: scroll;
}

.popup-inner {
    position: relative;
    padding: 32px;
    margin: 32px;
    width: 100%;
    background-color: White;
}

.popup-inner .close-btn {
    display: block;
    border-radius: 50%;
    position: absolute;
    top: 16px;
    right: 16px;
    border-color: transparent;
    background-color: antiquewhite
}

img {
    max-width: 100%;
    max-height: 40vh;
    object-fit: contain;
}
.loading
{
    margin-top: 20px;
}

.eduLink
{
    background-color: transparent;
    color: Black;
    transition-duration: 0.3s;
    text-decoration: none;
    text-align: center;
    padding: 10px;
    margin-top: 20px;
    border-radius: 10px;
    border-color: rgb(0, 110, 255);
    font-size: medium;
    cursor: pointer;
    border-width: 2px;
    border-style: solid;
}

.eduLink:hover
{
    background-color: rgb(57, 245, 0);
    color: white;
    border-color: rgb(57, 245, 0);
}
#battle{
    padding: 50px;
}

.battle-button{
    margin-top: 30px;
}

.battle-textbox{
    margin-left: 15px;
    margin-right: 15px;
    margin-top: 50px;
    margin-bottom: 20px;
    width: 100%;
}

.observation-battle-image{
    margin-bottom: 15px;
}

.startBattleBox{
    position: relative;
    margin-left: calc(50% - 140px);
    top: calc(50% - 74px);
    width: 280px;
}

.startBattleButton{
    margin-bottom: 40px;
}
